.profileBlock {
    padding: 10px;
    display: grid;
    grid-template-areas: 'userPhoto titleBlock'
                         'download titleblock'
                         'profileData profileData';
    grid-template-columns: 210px 1fr;
}

.userPhoto {
    grid-area: userPhoto;
    width: 200px;
    height: 200px;
    margin: 0;
    border: 5px solid lightblue;
    border-radius: 5%;
}

.downloadPhoto {
    grid-area: download;
    margin-top: 15px;
}

.titleBlock {
    grid-area: titleBlock;
}

.userName {
    margin: 0;
    margin-left: 200px;
    font-family:fantasy;
    word-spacing: 10px;
    letter-spacing: 3px;
    text-decoration: underline;
    text-decoration-thickness: 5px;
    text-decoration-color: lightblue;
    text-underline-offset: 10px;
}

.profileData {
    grid-area: profileData;
}

.listTheme {
    font-weight: 500;
}

.listDescription {

}

.jobPhoto {
    width: 30px;
    vertical-align: middle;
    margin-left: 5px;
}

.listDescription a {
    color: black;
    text-decoration: none;
    font-style: italic;
}