.dialogs {
    display: grid;
    grid-template-areas: "i m"
                         ". t";
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    grid-area: i;
    padding: 10px;
}

.dialogsItems .active {
    color: gold;
}

.dialogsItems img {
    width: 40px;
}

.messages {
    grid-area: m;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.message {
    color: white;
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 10%;
    padding: 5px;
}

.message.leftMessage {
    align-self: flex-start;
}

.message.rightMessage {
    align-self: flex-end;
}

.message.leftMessage::before {
    content: '';
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: red;
    display: inline-block;
    margin-right: 10px;
    vertical-align: baseline;
}

.message.rightMessage::after {
    content: '';
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: red;
    display: inline-block;
    margin-left: 10px;
    vertical-align: baseline;
}

.addMessageBlock {
    grid-area: t;
}

.addMessageBlock textarea {
    width: 50%;
}
