.header {
  grid-area: h;
  background-color: peru;
}

.logo {
  height: 100%;
}

.loginBlock {
  float: right;
}

.loginBlock a {
  color: white;
}

.trueLoginBlock {
  display: grid;
  grid-template-areas: 'login photo'
                       'logout photo';
}

.userLogin {
  grid-area: login;
}

.logout {
  grid-area: logout;
  height: 20px;
  margin: 0;
}

.userPhoto {
  grid-area: photo;
  margin: 10px 20px;
  height: 30px;
  width: 30px;
  vertical-align: middle;
  border-radius: 50%;
  border: 2px solid lightblue;
}

.defaultLogin {
  display: inline-block;
  margin: 15px 20px;
}